import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { ToastContainer, toast } from 'react-toastify'; // ToastContainer 
import 'react-toastify/dist/ReactToastify.css';
import '../styles/SignIn.css';

const SignIn = () => {
  const [isLogin, setIsLogin] = useState(true);
  const [animating, setAnimating] = useState(false);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const [confirmPassword, setConfirmPassword] = useState('');
  const [rememberMe, setRememberMe] = useState(false);
  const [termsAccepted, setTermsAccepted] = useState(false);

  const navigate = useNavigate();

  useEffect(() => {
    // Remember me  
    const storedEmail = localStorage.getItem('rememberedEmail');
    if (storedEmail) {
      setEmail(storedEmail);
      setRememberMe(true);
    }
  }, []);

  const toggleMode = () => {
    setAnimating(true);
    setTimeout(() => {
      setIsLogin(!isLogin);
      setAnimating(false);
    }, 500);
  };

  const handleLogin = async (e) => {
    e.preventDefault();
    //   
    if (!validateEmail(email)) {
      toast.error('   .');
      return;
    }
    //  
    const storedUser = JSON.parse(localStorage.getItem('user'));
    if (storedUser && storedUser.email === email && storedUser.password === password) {
      // TMDB API    ( API  )
      try {
        await axios.get('https://api.themoviedb.org/3/movie/550', {
          headers: {
            Authorization: `Bearer ${password}`,
          },
        });
        //   
        toast.success(' !');
        if (rememberMe) {
          localStorage.setItem('rememberedEmail', email);
        } else {
          localStorage.removeItem('rememberedEmail');
        }
        localStorage.setItem('isLoggedIn', 'true');
        navigate('/home');
      } catch (error) {
        toast.error('API   .');
      }
    } else {
      toast.error('   .');
    }
  };

  const handleSignUp = (e) => {
    e.preventDefault();
    //   
    if (!validateEmail(email)) {
      toast.error('   .');
      return;
    }
    //   
    if (password !== confirmPassword) {
      toast.error('  .');
      return;
    }
    //   
    if (!termsAccepted) {
      toast.error('  .');
      return;
    }
    //   
    const user = { email, password };
    localStorage.setItem('user', JSON.stringify(user));
    toast.success(' !   .');
    setIsLogin(true);
    setPassword('');
    setConfirmPassword('');
  };

  const validateEmail = (email) => {
    //    
    const regex = /\S+@\S+\.\S+/;
    return regex.test(email);
  };

  return (
    <div style={{ position: 'relative', width: '100%', height: '100vh', overflow: 'hidden' }}>
      {/*   */}
      <div
        className="background-overlay"
        style={{
          backgroundImage: `url(${process.env.PUBLIC_URL}/starwars.jpg)`,
          backgroundSize: 'cover',
          backgroundPosition: 'center',
          backgroundRepeat: 'no-repeat',
          position: 'fixed',
          top: 0,
          left: 0,
          width: '100%',
          height: '100%',
          zIndex: -1,
          filter: 'brightness(0.35)',
        }}
      ></div>

      {/*    */}
      <div className="aneis">
        <i style={{ '--clr': '#ED6A66' }}></i>
        <i style={{ '--clr': '#00239C' }}></i>
        <i style={{ '--clr': '#ffffff' }}></i>

        {/*   */}
        <div
          className={`login-container ${animating ? 'animating' : ''} ${
            isLogin ? 'login-mode' : 'signup-mode'
          }`}
          style={{
            position: 'relative',
            top: '0%',
            left: '0%',
            zIndex: 1,
          }}
        >
          <h2>{isLogin ? '' : ''}</h2>
          <p className="subtitle">
            {isLogin
              ? '    '
              : '    '}
          </p>

          <form onSubmit={isLogin ? handleLogin : handleSignUp}>
            <label htmlFor="email"></label>
            <input
              type="text"
              id="email"
              placeholder="email@example.com"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />

            <label htmlFor="password"></label>
            <input
              type="password"
              id="password"
              placeholder=" "
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />

            {!isLogin && (
              <>
                <label htmlFor="confirm-password"> </label>
                <input
                  type="password"
                  id="confirm-password"
                  placeholder=" "
                  value={confirmPassword}
                  onChange={(e) => setConfirmPassword(e.target.value)}
                />

                <div className="terms">
                  <input
                    type="checkbox"
                    id="terms"
                    checked={termsAccepted}
                    onChange={(e) => setTermsAccepted(e.target.checked)}
                  />
                  <label htmlFor="terms"> .</label>
                </div>
              </>
            )}

            {isLogin && (
              <>
                <div className="remember-me">
                  <input
                    type="checkbox"
                    id="rememberMe"
                    checked={rememberMe}
                    onChange={(e) => setRememberMe(e.target.checked)}
                  />
                  <label htmlFor="rememberMe">    </label>
                </div>
                <button
                  className="forgot-password"
                  onClick={(e) => {
                    e.preventDefault();
                    alert('>   <');
                  }}
                >
                   ?
                </button>
              </>
            )}

            <button className="login-btn" type="submit">
              {isLogin ? '' : ''}
            </button>
          </form>

          <button className="signup-btn" onClick={toggleMode}>
            {isLogin ? ' ?' : ' '}
          </button>
        </div>
      </div>

      {/* ToastContainer  */}
      <ToastContainer
        position="top-center"
        autoClose={3000}
        hideProgressBar={false}
        closeOnClick
        pauseOnHover
        draggable
      />
    </div>
  );
};

export default SignIn;
